.modal_content{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 auto;
    max-width: 600px;
    width: 90%;
    border-radius: 10px;
    padding: 20px;
    background-color: #fff;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
}

.modal_content p{
    text-align: center;
    font-size: 1.2rem;
    margin: 10px 0;
}

button{
    padding: 10px 15px;
    font-size: 1.4rem;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    color: #fff;
    background-color: #007fff;
    box-shadow: inset 2px 2px 5px rgba(255, 255, 255, 0.617),
                inset -2px -2px 5px rgba(43, 42, 42, 0.656);
}

.modal_content button{
    align-self: flex-end;
    font-size: 1.2rem;
}